.sidebar {
  flex: 3;
  margin: 20px;
  padding-bottom: 30px;
  background-color: #fdfbfb;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .sidebarItem {
    display: flex;
    flex-direction: column;
    align-items: center;

    img {
      margin-top: 15px;
      width: 80%;
    }

    p {
      padding: 30px;
      text-align: center;
    }

    ul {
      list-style-type: none;
      text-align: center;
      margin-bottom: 30px;

      li {
        display: inline-block;
        margin-top: 15px;
        cursor: pointer;
      }
    }

    .sidebarSocial {
      margin-top: 15px;
      width: 250px;
      display: flex;
      align-items: center;
      justify-content: center;

      i {
        font-size: 20px;
        margin-left: 10px;
        cursor: pointer;
      }
    }

    span {
      margin: 10px;
      padding: 5px;
      width: 80%;
      border-top: 1px solid #a7a4a4;
      border-bottom: 1px solid #a7a4a4;
      font-size: 12px;
      color: #222;
      font-weight: 600;
      line-height: 20px;
      text-align: center;
    }
  }
}
